    mask: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat center;
    mask-size: var(--icon-size);
}
body {
    display: grid;
    gap: 1rem;
    grid-template-rows: 0.5fr 1fr 0.5fr;
    max-width: 100vw;
    min-height: 150vh;
}

header {
