        flex-direction: column;
        gap: 1rem;
        padding: 2rem;
        max-width: 120ch;
        max-height: 420px;
    }

section.header-area {
    display: flex;
    justify-content: center;
    align-items: center;
    }
.example {
    anchor-name: --about-me-pic;
    position: relative;
    flex: 0 0 1;
    background: rgb(169, 193, 238);
    min-width: 400px;
    aspect-ratio: 1;
    align-content: center;
    text-align: center;
    box-shadow: var(--shadow) 0px 5px 4px;
}
.icon-area {
    display: flex;
    gap: 1rem;
    justify-content: flex-end;
}

div.background {
    min-width: 4000px;
