    .card-wrapper {
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;
        justify-content: center;

        section.card {
            width: var(--project-size);
            height: var(--project-size);
            box-shadow: var(--shadow) 0px 5px 4px;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
        }
    }
    .share-area, .title-area {
        display: flex;
        gap: 1rem;
        justify-content: space-between;
        align-items: center;
    }
    .project-area {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        padding: 1rem;
        background: white;
        width: var(--project-size);
        height: 50%;
        overflow: hidden;
    }
}

footer {
    max-width: 100vw;
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    background: var(--default);
    min-height: 60dvh;
    padding: 2rem;
    place-self: stretch;
    color: white;
}

.footer-wrapper {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    justify-content: space-between;
    align-items: flex-start;
}
.footer-wrapper > p {
    max-width: 70ch;
}
.footer-wrapper > * {
    padding-left: 2rem;
}
